<template>
    <BaseModal
        name="artist-kebab"
        :mobileMode="true"
        :closeOutside="true"
        :isLoading="data.loading"
    >
        <template v-slot:title>
            <div class="title">
                <div class="img" @click="gallery">
                    <Img v-if="data.item?.profileImage?.resized"
                        :preview="data.item?.profileImage?.resized"
                        :original="data.item?.profileImage?.original"
                        :alt="data.item?.name"
                        aspectRatio="1 / 1"
                    />
                </div>
                <span>{{ data.item?.name }}</span>
            </div>
        </template>
        <template v-slot:content>
            <ul class="kebab-list">
                <li class="clickable" @click="shareIt">
                    <Icon icon="share"/>
                    <span>{{ t('modals.artist-kebab.share') }}</span>
                </li>
                <li class="clickable" @click="about">
                    <Icon icon="help"/>
                    <span>{{ t('modals.artist-kebab.about') }}</span>
                </li>
            </ul>
        </template>
    </BaseModal>
</template>

<script setup>
import { onMounted, reactive } from 'vue';
import { useI18n } from 'vue-i18n';

import Img from 'ðŸ”—/components/ui/Img.vue';
import BaseModal from 'ðŸ”—/components/modals/BaseModal.vue';
import stores from '@/stores';

import api from 'ðŸ”—/api';
import { message } from 'ðŸ”—/utils/utils';

const { t } = useI18n();

const tg = stores.tg();
const modals = stores.modals();
const share = stores.share();

const data = reactive({
    loading: true
});


const shareIt = () => {
    close();
    
    share.artist(data.item);
}

const about = () => {
    close();

    modals.open('artist', { quietClose: true, data: {
        code: data.item.code,
        item: data.item
    } });
}

const gallery = () => {
    if (!data.item?.id || !data.item?.profileImage?.original) {
        return;
    }

    modals.open('gallery', {
        quietClose: true,
        data: { imgs: [{
            title: data.item.name,
            ...data.item?.profileImage
        }] } });
}


const like = () => {
    message.info(t('common.soon'));
}

const close = () => {
    modals.close('artist-kebab');
};


onMounted(async () => {
    const payload = modals.getData('artist-kebab');

    if (!payload?.code) {
        close();
        return;
    }

    if (payload.item) {
        data.item = payload.item;
    } else {
        data.item = await api.artists().getByCode(payload.code);
    }

    data.loading = false;
});

</script>

<style lang="scss" scoped>
    .title {
        display: flex;
        width: 100%;
        align-items: center;
        gap: 24px;
        flex-direction: column;
        padding-top: 12px;

        .img {
            width: 74px;
            height: 74px;
            border-radius: 50%;
            @include animage-booble-shadow;

            img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
            }
        }
    }

    ul.kebab-list {
        @include settings-list;
        margin-top: 24px;
    }
</style>
